import React, { Fragment, useState, useEffect } from "react";
import {
  ListsWidget,
  ListsWidgetHeader,
  ListsWidgetBody,
} from "../../../../widgets/listsWidget/ListsWidget";
import { Table, Spinner } from "react-bootstrap";
import { getTopPerformers } from "../../curd/news";
import { toast } from "react-toastify";
import cuid from "cuid";
import Skeleton from "@material-ui/lab/Skeleton";

const TopTwentyPerformers = () => {
  const [data, setData] = useState(null);

  useEffect(() => {
    // console.log("called" + state.contactProfileId);
    if (!data) {
      getTopPerformers()
        .then(({ data }) => {
          setData(data);
        })
        .catch((err) => {
          toast.error("Data Fetching Error");
        });
    }
  }, []);
  return (
    <Fragment>
      <ListsWidget className="gutter-b p-3">
        <ListsWidgetHeader className="title" title="Top Twenty Performers" />
        <ListsWidgetBody>
          {data ? (
            <Table responsive>
              <thead>
                <tr>
                  <th>Rank</th>
                  <th>Name</th>
                  <th>Ls</th>
                  <th>L</th>
                  <th>S</th>
                  <th>F</th>
                  <th>Total</th>
                </tr>
              </thead>

              <tbody>
                <Fragment>
                  {Object.entries(data).map(([key, value], index) => {
                    return <tr key={cuid()}>
                          <td>{parseInt(index) + 1}</td>
                          <td>{key}</td>
                          <td>{value.ls}</td>
                          <td>{value.l}</td>
                          <td>{value.s}</td>
                          <td>{value.f}</td>
                          <td>{value.total}</td>
                        </tr>;
                  })}
                </Fragment>
              </tbody>
            </Table>
          ) : (
            <div>
            <Skeleton animation="wave" variant="text" />
            <Skeleton animation="wave" variant="text" />
            <Skeleton animation="wave" variant="rect" width="100%" height={118} />
          </div>
          )}
        </ListsWidgetBody>
      </ListsWidget>
    </Fragment>
  );
};

export default TopTwentyPerformers;
