import React, { Fragment, useState, useEffect } from "react";
import {
  ListsWidget,
  ListsWidgetHeader,
  ListsWidgetBody,
} from "../../../../widgets/listsWidget/ListsWidget";
import { Tabs, Tab } from "react-bootstrap";
import NewsRowItem from "../../partials/NewsRowItem";
import { getTopOfficesByListings, getTopOfficesBySales } from "../../curd/news";
import cuid from "cuid";
const TopTenOffices = () => {
  const [key, setKey] = useState("Listings");
  const [listings, setListings] = useState(null);
  const [sales, setSales] = useState(null);

  useEffect(() => {
    if (!listings) {
      const fetchData = async () => {
        try {
          const [listings, sales] = await Promise.all([
            getTopOfficesByListings(),
            getTopOfficesBySales(),
          ]);
          setListings(listings.data.data);
          setSales(sales.data.data);
        } catch (err) {
          console.log(err);
        }
      };

      fetchData();
    }
  }, []);
  return (
    <Fragment>
      <ListsWidget className="gutter-b p-3">
        <ListsWidgetHeader className="title" title="Top 10 offices" />
        <ListsWidgetBody>
          <Tabs
            className="equal-tab"
            activeKey={key}
            onSelect={(k) => setKey(k)}
          >
            <Tab eventKey="Listings" title="Listings">
              <Fragment>
                {listings ? (
                  <Fragment>
                    {Object.entries(listings).map(([key, value]) => {
                      return (
                        <NewsRowItem
                        key={cuid()}
                          id={parseInt(key) + 1}
                          title={value.name}
                          value={value.total_listings}
                        />
                      );
                    })}
                  </Fragment>
                ) : (
                  "loading"
                )}
              </Fragment>
            </Tab>
            <Tab eventKey="Sales" title="Sales">
              <Fragment>
              {sales ? (
                  <Fragment>
                    {Object.entries(sales).map(([key, value]) => {
                      return (
                        <NewsRowItem
                        key={cuid()}
                          id={parseInt(key) + 1}
                          title={value.name}
                          value={value.total_sales}
                        />
                      );
                    })}
                  </Fragment>
                ) : (
                  "loading"
                )}
              </Fragment>
            </Tab>
          </Tabs>
        </ListsWidgetBody>
      </ListsWidget>
    </Fragment>
  );
};

export default TopTenOffices;
