import React, { useEffect, Fragment, useState } from "react";
import SocialFeedCard from "./SocialFeedCard";
import { makeStyles } from "@material-ui/styles";
import SocialFeedForm from "./SocialFeedForm";
import { ListsWidgetBody, ListsWidget } from "../../../../widgets/listsWidget/ListsWidget";
import { getAllPosts, getPostApproved, getPostDelete, getPostLike } from "../../curd/news";
import { toast } from "react-toastify";
import { Waypoint } from "react-waypoint";
import TableWithDrawer from "../../../../partials/layout/pageLayout/TableWithDrawer";
import DrawerForm from "../../partials/DrawerForm";
import Skeleton from "@material-ui/lab/Skeleton";
const queryString = require("query-string");


const useStyles = makeStyles(theme => ({
  socialFeed: {
    maxHeight: '600px',

  },
  socialFeedBody: {
    overflow: 'scroll',
    maxHeight: '85vh',
    padding: '0px !important'

  }
}));

const SocialFeeds = () => {

  const [items, setItems] = useState(null);
  const [otherResData, setoOtherResData] = useState(null);
  const [open, setOpen] = useState(false);
  const [filterValues, setFilterValues] = useState(null);

  useEffect(() => {
    update();
  }, []);

  const update = (e) => {
    getAllPosts(e)
      .then((res) => {

        const { data, ...rest } = { ...res.data }
        setItems(data)
        setoOtherResData(rest)
        console.log(data);
        console.log(rest);
      })
      .catch((e) => {
        toast.error('Error while fetching');
      });
  }
  const classes = useStyles();

  const handleLikeClick = (e) => {
    getPostLike(e)
      .then(({ data }) => {
        console.log(data)
        toast.success(data.message);
        update();
      })
      .catch((e) => {

        toast.error('Error while fetching');
      });
    console.log(e)
  }
  const handlePostDelete = (e) => {
    getPostDelete(e)
      .then(({ data }) => {
        console.log(data)
        toast.success(data.message);
        update();
      })
      .catch((e) => {
        toast.error('Error while fetching');
      });
    console.log(e)
  }
  const handlePostApprove = (e) => {
    getPostApproved(e)
      .then(({ data }) => {
        console.log(data)
        toast.success(data.message);
        update();
      })
      .catch((e) => {
        toast.error('Error while fetching');
      });
    console.log(e)
  }

  const handleFetchMoreData = () => {

    let query = queryString.stringify(
      { page: otherResData.current_page+1},
      filterValues ? { from: null } : ''
    );
    console.log(query)
    console.log('more data ')
    getAllPosts(query)
    .then((res) => {

      const { data, ...rest } = { ...res.data }

      setItems(data.concat(items))
      setoOtherResData(rest)
    })
    .catch((e) => {
      toast.error('Error while fetching');
    });
  }

  const handleDrawer = () => {
    console.log('clicked')
    setOpen(!open);
  }

  const handleDrawerSubmit = (values) => {

    console.log(values)
    let formData = new FormData();
    for (var key in values) {
      formData.append(key, values[key]);
    }

    console.log(formData)
  }

  return (
    <Fragment>

      <ListsWidget className="socialFeed" fluidHeight={false}>
        <ListsWidgetBody className={classes.socialFeedBody}  >

          {
            items ? <Fragment>
              <SocialFeedForm handleDrawer={handleDrawer} update={update} />
              {items?.map((item, index) => (
                <SocialFeedCard data={item} handlePostApprove={handlePostApprove} handlePostDelete={handlePostDelete} handleLikeClick={handleLikeClick} key={index} />
              ))}
               <Waypoint onEnter={handleFetchMoreData} />
            </Fragment> : <div>
                <Skeleton animation="wave" variant="text" />
                <Skeleton animation="wave" variant="text" />
                <Skeleton animation="wave" variant="rect" width="100%" height="77vh" />
              </div>
          }

        </ListsWidgetBody>
      </ListsWidget>


      <TableWithDrawer
        handleDrawer={open}
        drawerClosed = {()=> setOpen(false)}
        title="Posts"
        drawer={<Fragment>
          <DrawerForm onSubmit={handleDrawerSubmit} />
        </Fragment>}
      />
    </Fragment>
  );
};

export default SocialFeeds;
