import React, { Fragment } from "react";
import NewsRowItem from "../../partials/NewsRowItem";
import { Spinner } from "react-bootstrap";

const RatioItem = ({ data }) => {
  return (
    <Fragment>
      {data ? (
        <Fragment>
          {Object.entries(data).map(([key, value], index) => {
            return (
              <NewsRowItem
                key={index}
                id={parseInt(index) + 1}
                title={value.team}
                value={value.ratio_text}
              />
            );
          })}
        </Fragment>
      ) : (
        <Spinner animation="grow" />
      )}
    </Fragment>
  );
};

export default RatioItem;
