import React, { forwardRef } from 'react'
import './_listWidget.scss'
import clsx from 'clsx'

export const ListsWidgetHeader = forwardRef(
    ({ fluidHeight, title ,className, ...props }, ref) => (
    <div className="card-header  border-0 m-auto">
    <h5 className={ className? className :"card-title font-weight-bolder brand-text"}>{title}</h5>
    </div>
    )
  );

  export const ListsWidget = forwardRef(
    ({ fluidHeight, className, ...props }, ref) => (
      <div
        {...props}
        ref={ref}
        className={clsx(
          "card card-custom",
        {"card-stretch": fluidHeight },
          className
        )}
      />
    )
  );


  export const ListsWidgetBody = forwardRef(
    ({ fit, fluid, className, ...props }, ref) => (
      <div
        {...props}
        ref={ref}
        className={clsx(
          "card-body pt-0",
          className
        )}
      />
    )
  );