import React, { Fragment, useState, useEffect } from "react";
import {
  ListsWidget,
  ListsWidgetHeader,
  ListsWidgetBody,
} from "../../../../widgets/listsWidget/ListsWidget";
import { Tabs, Tab } from "react-bootstrap";
import NewsRowItem from "../../partials/NewsRowItem";
import {
  getProspectingRatios,
  getListingRatios,
  getBuyerRatios,
} from "../../curd/news";
import { toast } from "react-toastify";
import RatioItem from "./RatioItem";

const TopRatios = () => {
  const [key, setKey] = useState("Prospecting");
  const [prospecting, setProspecting] = useState(null);
  const [listing, setListing] = useState(null);
  const [buyers, setBuyers] = useState(null);

  useEffect(() => {
    if (!prospecting) {
      const fetchData = async () => {
        try {
          const [prospectingData, listingData, buyersData] = await Promise.all([
            getProspectingRatios(),
            getListingRatios(),
            getBuyerRatios(),
          ]);
          setProspecting(prospectingData.data.data);
          setListing(listingData.data.data);
          setBuyers(buyersData.data.data);
        } catch (err) {
          toast.error("Data Fetching Error");
        }
      };

      fetchData();
    }
  }, []);
  return (
    <Fragment>
      <ListsWidget className="gutter-b p-3">
        <ListsWidgetHeader className="title" title="Top Ratios" />
        <ListsWidgetBody>
          <Tabs
            className="equal-tab"
            activeKey={key}
            onSelect={(k) => setKey(k)}
          >
            <Tab eventKey="Prospecting" title="Prospecting">
              <RatioItem data={prospecting} />
            </Tab>
            <Tab eventKey="Listing" title="Listing">
              <RatioItem data={listing} />
            </Tab>
            <Tab eventKey="Buyer" title="Buyer">
              <RatioItem data={buyers} />
            </Tab>
          </Tabs>
        </ListsWidgetBody>
      </ListsWidget>
    </Fragment>
  );
};

export default TopRatios;
