import React, { Fragment, useState, useEffect } from "react";
import {
  ListsWidget,
  ListsWidgetHeader,
  ListsWidgetBody,
} from "../../../../widgets/listsWidget/ListsWidget";
import NewsRowItem from "../../partials/NewsRowItem";
import { getTopPointsGame } from "../../curd/news";
import cuid from "cuid";
import Skeleton from "@material-ui/lab/Skeleton";

const PointsGame = () => {
  const [data, setData] = useState(null);

  useEffect(() => {
    // console.log("called" + state.contactProfileId);
    if (!data) {
      getTopPointsGame()
        .then(({ data: { data } }) => {
          setData(data);
          console.log(data);
        })
        .catch(() => {});
    }
  }, []);
  return (
    <Fragment>
      <ListsWidget className="gutter-b p-3">
        <ListsWidgetHeader className="title" title="Points Game" />
        <ListsWidgetBody>
          {data ? (
            <Fragment>
              {Object.entries(data).map(([key, value], index) => {
                return (
                  <NewsRowItem
                    key={cuid()}
                    id={parseInt(index) + 1}
                    title={key}
                    value={value.total}
                  />
                );
              })}
            </Fragment>
          ) : (
            <div>
              <Skeleton animation="wave" variant="text" />
              <Skeleton animation="wave" variant="text" />
              <Skeleton animation="wave" variant="rect" width="100%" height={118} />
            </div>
          )}
        </ListsWidgetBody>
      </ListsWidget>
    </Fragment>
  );
};

export default PointsGame;
