import React, { Fragment, useState, useEffect } from "react";
import {
  ListsWidget,
  ListsWidgetHeader,
  ListsWidgetBody,
} from "../../../../widgets/listsWidget/ListsWidget";
import NewsRowItem from "../../partials/NewsRowItem";
import { getTopLeaders } from "../../curd/news";
import { Spinner } from "react-bootstrap";
const TopTenLeaders = () => {
  const [data, setData] = useState(null);

  useEffect(() => {
    // console.log("called" + state.contactProfileId);
    if(!data){
        getTopLeaders()
        .then(({ data: { data } }) => {
          setData(data);
          console.log(data);
        })
        .catch(() => {});
    }

  }, []);
  return (
    <Fragment>
      <ListsWidget className="p-3 h-100">
        <ListsWidgetHeader className="title" title="Top 10 Leaders" />
        <ListsWidgetBody>
          {data ? (
            <Fragment> 
              {Object.entries(data).map(([key, value]) => {
              
              return  <NewsRowItem key={key} id={parseInt(key)+1} title={value.leader} />;
            })}
          </Fragment>
          ) : (
            <Spinner animation="grow" />
          )}

          
        </ListsWidgetBody>
      </ListsWidget>
    </Fragment>
  );
};

export default TopTenLeaders;
