import React, { useState, useRef, Fragment, useEffect } from "react";
import { connect } from "react-redux";
import { reduxForm, Field, formValueSelector } from "redux-form";
import { post } from 'axios';
// import validate from "../../partials/form/validate";
import {
  Form,
  Col,
  Row,
  OverlayTrigger,
  Popover,
  Tabs,
  Tab,
  Spinner,
} from "react-bootstrap";

import {
  combineValidators,
  composeValidators,
  isRequired,
  hasLengthGreaterThan,
} from "revalidate";
import { makeStyles } from "@material-ui/core/styles";
import clsx from "clsx";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import CardActions from "@material-ui/core/CardActions";
import IconButton from "@material-ui/core/IconButton";
import ShareIcon from "@material-ui/icons/Share";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import TextArea from "../../../../partials/form/TextArea";
import * as news from "../../_redux/newsRedux";
import { green } from "@material-ui/core/colors";
import ThemeButton from "../../../../partials/button/ThemeButton";
import { FileInputRef } from "../../../../partials/form/FileInputRef";
import TextInput from "../../../../partials/form/TextInput";
import MultiSelect from "../../../../partials/form/MultiSelect";
import { SingleToggleButton } from "../../../../partials/form/singleToggleButton/SingleToggleButton";
import { postPost } from "../../curd/news";
import { toast } from "react-toastify";
import { getAllSuburb } from "../../../../GeneralApi/GeneralApi";
import AsyncMultiSelect from "../../../../partials/form/AsyncMultiSelect";
const queryString = require("query-string");


const validate = combineValidators({
  socialFeedPost: composeValidators(
    isRequired({ message: "Please enter post" }),
    hasLengthGreaterThan(10)({
      message: "Post needs to be at least 10 characters",
    })
  )(),
});
const useStyles = makeStyles((theme) => ({
  card: {
    overflow: "visible",
    marginBottom: "1rem",
  },
  buttonSuccess: {
    backgroundColor: green[500],
    "&:hover": {
      backgroundColor: green[700],
    },
  },
  expand: {
    transform: "rotate(0deg)",
    marginLeft: "auto",
    transition: theme.transitions.create("transform", {
      duration: theme.transitions.duration.shortest,
    }),
  },
}));

const SocialFeedForm = (props) => {
  const inputFile = useRef(null);
  const timer = useRef();
  const { handleSubmit, pristine, submitting,reset, invalid, isPrivate ,update,handleDrawer} = props;
  const [videoUrlClicked, setVideoUrlClicked] = useState(false);
  const [suburb, setSuburb] = useState(null);


//Loading Effect
  const [loading, setLoading] = useState(false);
  const [success, setSuccess] = useState(false);
//Loading Effect

  const classes = useStyles();

  const top100Films = [
    { label: "The Shawshank Redemption", value: 19944 },
    { label: "The Godfather", value: 1972 },
    { label: "The Godfather: Part II", value: 1974 },
    { label: "The Dark Knight", value: 2008 },
    { label: "12 Angry Men", value: 1957 },
    { label: "Schindler's List", value: 1993 },
    { label: "Pulp Fiction", value: 1994 },
  ];
  const [keyTag, setKeyTag] = useState("Listing");
  const popover = (
    <Popover id="popover-basic">
      <Popover.Content>
        {!videoUrlClicked ? (
          <Fragment>
            {" "}
            <Field
              name="videos"
              component={FileInputRef}
              icon={<i class="fas fa-file-video"></i>}
            />
          </Fragment>
        ) : (
          ""
        )}

        <ThemeButton onClick={() => setVideoUrlClicked(!videoUrlClicked)} icon>
          <i class="fab fa-youtube"></i>
        </ThemeButton>
        {videoUrlClicked ? (
          <Row>
            <Field name="youtube_url" component={TextInput} />
          </Row>
        ) : (
          ""
        )}
      </Popover.Content>
    </Popover>
  );
  const getOptions = (text) => {

    let query = queryString.stringify(
      { name: text},
      { name: 30},
    );

   return  getAllSuburb(query)
      .then(({data}) => {
        console.log(data.data);
        let options = data.data.map( item => ({ value: item.id, label: item.name }));
        console.log(options)
        return options;
        // setSuburb(data.data)
      })
      .catch((e) => {
        toast.success('Error while saving');
      });
    // return axios.get('/categories')
    //   .then(function (response) {
    //     let options = response.data.map( category => ({ value: category.id, label: category.title }));
    //     return { options };
    //   })
    //   .catch(function (error) {
    //     console.log(error);
    //   });
  }
  const TagPopover = (
    <Popover id="popover-basic">
      <Popover.Content>
        <Tabs
          className="equal-tab"
          activeKey={keyTag}
          onSelect={(k) => setKeyTag(k)}
        >
          <Tab eventKey="Suburb" title="Suburb">
            <Fragment>
              <Field
                name="suburb"
                placeholder="Select type"
                component={AsyncMultiSelect}
                showBottom={false}
                isMulti
                options={getOptions}
              />
            </Fragment>
          </Tab>
         
          <Tab eventKey="Listing" title="Listing">
            <Fragment>
              <Field
                name="listing"
                placeholder="Select type"
                component={MultiSelect}
                showBottom={false}
                isMulti
                options={top100Films}
              />
            </Fragment>
          </Tab>
        </Tabs>
      </Popover.Content>
    </Popover>
  );

  const onFormSubmit = async (values) => {
    setSuccess(false);
    setLoading(true);
    let formData = new FormData();
    const files = values.images;
    for ( var key in values ) {
      if(key === 'images')
      { 
        for (let i = 0; i < files.length; i++) {
          formData.append(`images[${i}]`, files[i])
      }
      }
      else {
        formData.append(key, values[key]);
      }
  }

    postPost(formData)
      .then(({data}) => {
        console.log(data);
        console.log(data.images);
        
        setSuccess(true);
        setLoading(false);
        reset();
        toast.success('Post Saved Successfully');
        update()
      })
      .catch((e) => {
        setSuccess(true);
        setLoading(false);
        toast.success('Error while saving');
      });

 };

//  useEffect(() => {
//   getAllSuburb()
//   .then(({data}) => {
//     console.log(data);
//     setSuburb(data.data)
//   })
//   .catch((e) => {
//     toast.success('Error while saving');
//   });

// }, []);


  const onButtonClick = () => {
    // `current` points to the mounted file input element
    console.log(inputFile);
    inputFile.current.click();
  };
  console.log(isPrivate);
  return (<Fragment>
<Form onSubmit={handleSubmit(onFormSubmit)} noValidate>
      <Card className={classes.card}>
        <CardContent>
          <Form.Group as={Row}>
            <Col sm={12}>
            <div className="post-filter-button">
            <ThemeButton icon onClick={handleDrawer} ><i class="fas fa-search    "></i></ThemeButton>
          </div>
              <Field
                name="text"
                type="textarea"
                rows="3"
                component={TextArea}
                placeholder="Enter your post here"
              />
              <div className="d-none">
              </div>
            </Col>
          </Form.Group>
        </CardContent>
        <CardActions disableSpacing>
          <Field
            name="images"
            component={FileInputRef}
            icon={<i className="fas fa-file-image"></i>}
          />
          <OverlayTrigger trigger="click" placement="right" overlay={popover}>
            <ThemeButton icon>
              <i class="fas fa-video    "></i>
            </ThemeButton>
          </OverlayTrigger>

          <OverlayTrigger
            rootClose
            trigger="click"
            placement="right"
            overlay={TagPopover}
          >
            <ThemeButton icon>
              <i class="fas fa-tags"></i>
            </ThemeButton>
          </OverlayTrigger>

          <Field
            name="isPrivate"
            component={SingleToggleButton}
            icon
            label={ <i class="fas fa-unlock"></i>}
            positiveIcon = {<i class="fas fa-lock"></i>}
          />
          <IconButton aria-label="Share">
            <ShareIcon />
          </IconButton>
          <IconButton className={clsx(classes.expand)} aria-label="Show more">
            <ExpandMoreIcon />
          </IconButton>
          <ThemeButton
            type="submit"
            disabled={invalid || pristine || submitting || loading}
          > {loading &&   <Spinner
            as="span"
            animation="grow"
            size="sm"
            role="status"
            aria-hidden="true"
          />}
          
            Post
          </ThemeButton>
        </CardActions>
      </Card>
    </Form>
    
  </Fragment>
    
  );
};
const selector = formValueSelector("socialFeedForm");
const actions = {
  socialFeedPost: news.actions.socialFeedPostAsync,
};

const mapState = (state) => {
  const isPrivate = selector(state, "isPrivate");
  return {
    isPrivate,
  };
};
export default connect(
  mapState,
  actions
)(reduxForm({ form: "socialFeedForm", validate })(SocialFeedForm));
