import React, { useEffect } from "react";
import { Field, reduxForm } from "redux-form";
import { Form, Row, Col } from "react-bootstrap";
import ThemeButton from "../../../partials/button/ThemeButton";
import DateInput from "../../../partials/form/DateInput";
import MultiSelect from "../../../partials/form/MultiSelect";
import { toast } from "react-toastify";
import { getAllMembers } from "../../../GeneralApi/GeneralApi";

const validate = (values) => {
  const errors = {};
  if (!values.username) {
    errors.username = "Required";
  } else if (values.username.length > 15) {
    errors.username = "Must be 15 characters or less";
  }
  if (!values.email) {
    errors.email = "Required";
  } else if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values.email)) {
    errors.email = "Invalid email address";
  }
  if (!values.age) {
    errors.age = "Required";
  } else if (isNaN(Number(values.age))) {
    errors.age = "Must be a number";
  } else if (Number(values.age) < 18) {
    errors.age = "Sorry, you must be at least 18 years old";
  }
  return errors;
};

const DrawerForm = (props) => {
  const { handleSubmit, pristine, submitting } = props;
  const status = [
    {
      label: "Pending",
      value: "1",
    },
    {
      label: "Approved",
      value: "2",
    },
  ];

  useEffect(() => {
    getAllMembers()
      .then((res) => {
        console.log(res);
      })
      .catch((e) => {
        toast.error('Error while fetching');
      });
  }, []);


  
  return (
    <form onSubmit={handleSubmit}>
      <Form.Group as={Row}>
        <Col className="pb-1" sm={12}>
          <Field
            name="from"
            type="text"
            component={DateInput}
            placeholder="Date From"
          />
        </Col>
        <Col className="pb-1" sm={12}>
          <Field
            name="to"
            type="text"
            component={DateInput}
            placeholder="Date To"
          />
        </Col>
        <Col className="pb-1" sm={12}>
          <Field
            name="created_by"
            placeholder="Posted By"
            component={MultiSelect}
            showBottom={false}
            isMulti={false}
            options={status}
          />
        </Col>
        <Col className="pb-1" sm={12}>
          <Field
            name="status"
            placeholder="Status"
            component={MultiSelect}
            showBottom={false}
            isMulti={false}
            options={status}
          />
        </Col>
      </Form.Group>
      <div>
        <ThemeButton
          disabled={pristine || submitting}
          className="white-outline-button"
          type="submit"
        >
          Filter
        </ThemeButton>
      </div>
    </form>
  );
};

export default reduxForm({
  form: "postFilterForm", // a unique identifier for this form
  validate,
})(DrawerForm);
