import React, { useCallback, useState, useEffect, Fragment } from "react";
import { useDropzone } from "react-dropzone";
import { Chip } from "@material-ui/core";
import { Image } from "react-bootstrap";
import ThemeButton from "../button/ThemeButton";

const centralDivStyle = {
  textAlign: "center",
};
const adaptFileEventToValue = delegate => e => delegate(e.target.files[0]);
export const FileInputRef = (props) => {
  const {
    input: { onChange },
    disabled,
    inputFileRef,
    icon,
  } = props;

  const [files, setFiles] = useState([]);
  const { getRootProps, getInputProps, open, acceptedFiles } = useDropzone({
    accept: "image/*",
    multiple: true,
    onDrop: (acceptedFiles) => {
      setFiles(
        acceptedFiles.map((file) =>
          Object.assign(file, {
            preview: URL.createObjectURL(file),
          })
        )
      );
      onChange(acceptedFiles);
    },
    noClick: true,
    noKeyboard: true,
  });

  useEffect(
    () => () => {
      // Make sure to revoke the data uris to avoid memory leaks
      files.forEach((file) => URL.revokeObjectURL(file.preview));
    },
    [files]
  );

  if (disabled) {
    return null;
  }
console.log(acceptedFiles)
  return (
    <Fragment>
      <div {...getRootProps()} style={centralDivStyle}>
        <input ref={inputFileRef} {...getInputProps()} />
        <ThemeButton className={acceptedFiles.length > 0 ? "green-icon-button": ''} icon onClick={open}>
          {" "}
          {icon}
        </ThemeButton>
      </div>
    </Fragment>
  );
};
